@import url("https://fonts.googleapis.com/css?family=Didact+Gothic&display=swap");
* {
  font-family: "Didact Gothic", sans-serif !important;
}
.words-main-container {
  border-radius: 5px;
  margin-top: 15px;
  height: 75px;
  width: 96vw;
  display: flex;
  justify-content: center;
}

.words {
  display: grid;
  height: 100%;
  width: 98%;
  text-align: center;
  grid-template-columns: repeat(5, 1fr);
  font-size: 12px !important;
}
.words div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.sentences-container {
  margin-top: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  width: 96vw;
  background-color: white;
  display: flex;
  justify-content: center;
}
.sentences-container > div {
  margin-bottom: 20px;
  margin-top: 20px;
  width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
